import React from "react"
import AniLink from "gatsby-plugin-transition-link/AniLink"
import { Splide, SplideSlide } from "@splidejs/react-splide"
import "@splidejs/splide/dist/css/themes/splide-default.min.css"
import Layout from "../components/layout"
import SEO from "../components/seo"
import ProjectTile from "../components/projectTile"
import NavigationBar from "../components/navigationBar"
import "../components/page.css"
import Typewriter from "typewriter-effect"

const ProjectPage = () => (
  <Layout>
    <SEO title="My Projects" />
    <NavigationBar active="projects" color="#562a60" />
    <div className="description">
      <Typewriter
        onInit={typewriter => {
          typewriter
            .changeDeleteSpeed(2)
            .changeDelay(30)
            .pauseFor(300)
            .typeString(
              "Feel free to browse through some projects I've worked on below."
            )
            .pauseFor(800)
            .typeString(" They're small,")
            .pauseFor(400)
            .typeString(" but fun.")
            .pauseFor(800)
            .typeString(" Like me.")
            .pauseFor(200)
            .deleteChars(8)
            .start()
        }}
      />
    </div>
    <Splide>
      <SplideSlide>
        <div>
          <ProjectTile
            name="Guess the Colour Game"
            link="https://canams.github.io/colour-game/"
            description="A fun HTML5, CSS3 and ES6 game"
          />
        </div>
      </SplideSlide>
      <SplideSlide>
        <div>
          <ProjectTile
            name="Minesweeper"
            link="https://canams.github.io/minesweeper/"
            description="A Java implementation of the popular Minesweeper game"
          />
        </div>
      </SplideSlide>
      <SplideSlide>
        <div>
          <ProjectTile
            name="Chatbot"
            description="Implemented in Python using NLTK. *coming soon*"
          />
        </div>
      </SplideSlide>
      <SplideSlide>
        <div>
          <ProjectTile
            name="Quiz"
            link="https://canams.github.io/quiz/"
            description="Get to know me a little more with this simple HTML5, CSS and JavaScript quiz"
          />
        </div>
      </SplideSlide>
    </Splide>
  </Layout>
)

export default ProjectPage
